

    import {Input} from '../../../src'
    import {ref} from 'vue'

    export default {
        name: "demo-input",
        setup() {
            const text = ref('hello world')
            const showPrepend = ref(true)

            const input = Input.use.ref('myInput')
            const input2 = Input.use.ref()

            return {
                text,
                showPrepend,
                outerClear: () => {
                    input.value!.methods.clear()
                },

                input2,
                outerClear2: () => {
                    // console.log(input2)
                    input2.value!.methods.clear()
                }
            }
        },
    }
